:root {
    --primary-clr: #fff;
}


.navbar { 
    background-color: transparent;
    padding: 1em 0;
}
.logo {
    text-decoration: none;
    font-size: 2rem;
    font-weight: 600;
    color: var(--primary-clr)
}
.navbar-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu-icon {
    display: flex;
    align-items: center;
    border-radius: 100%;
    color: var(--primary-clr);
}

.nav-menu.active {
  left: 0;
}
.nav-menu {
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: start;
    width: 100%;
    height: 93vh;
    position: absolute;
    top: 75px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    background-image: url(../images/rem.png);
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: clamp(18.75rem, 13.3929rem + 21.4286vw, 37.5rem);
    z-index: 1;
}
.nav-links {
    text-decoration: none;
    text-transform: uppercase;
    display: inline-block;
    width: 100%;
    padding: 2em 0;
    font-weight: 600;
    box-shadow: inset 0 0 0 0 lightblue;
    transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
}
.nav-links:hover {
    box-shadow: inset 1000px 0 0 0 lightseagreen;
    color: white;
}
.nav-item {  
    text-align: center;
}
.nav-item:hover {
    cursor: pointer;
}

@media (min-width: 850px) {

    .nav-menu.active {
        left: auto;
    }
    .nav-menu {
        background-color: transparent;
        position: relative;
        background-image: none;
        height: auto;
        left: auto;
        top: auto;
        flex-direction: row;
        justify-content: space-between;
        width: 310px;
        transition: none;
    }
    .nav-item a.active {
        text-decoration: 2px solid  white underline;
    }
    .menu-icon {
        display: none;
    }
    .nav-links:hover {
        box-shadow: none;
    }
    .nav-links {
        color: white;
        transition: none;
        padding: 0 0;
    }
}


